import React from "react";
import { Box, Paper, Typography, Button, useMediaQuery, useTheme } from "@mui/material";
import { NumberInput } from '../../component/input';
import { ItemProduct } from '../index';


type DescriptionSegmentProps = {
    item: ItemProduct
    nameComponent?: React.ReactNode 
}


const BlockPrice =({ item })=> {
    const calculate =()=> item.price.current + (item.price.current * 0.1);


    return(
        <Paper
            elevation={0}
            sx={{
                display: 'flex',
                alignItems: 'center',
                height: 80,
                mt: 3,
                px: {
                    xs: 1, // padding   xs ( 600px)
                    sm: 2, // padding   sm (600px  )
                    md: 3, // padding   md (960px  )
                    lg: 4, // padding   lg (1280px  )
                    xl: 5, // padding   xl (1920px  )
                },
                borderRadius: '15px',
            }}
        >
            
            <Box 
                sx={{ 
                    minWidth: '180px', 
                    display: 'flex' 
                }}
            >
                <Typography color="black"
                    sx={{
                        fontFamily: 'Kinetika, sans-serif',
                        fontWeight: 800,
                        fontSize: '28px',
                        lineHeight: '1', // 100%
                        letterSpacing: '0px', // 0%
                        textTransform: 'uppercase',
                        textAlign: 'center'
                    }}
                >
                     { item.price?.current } 
                </Typography>
                <Typography color="black"
                    sx={{
                        textDecoration: 'line-through',
                        fontFamily: 'Kinetika, sans-serif',
                        mt: 0.5,
                        ml: 1,
                        fontWeight: 800,
                        fontSize: '22px',
                        lineHeight: '1',
                        textAlign: 'center',
                        color: '#9F9F9F'
                    }}
                >
                    { item?.price?.old || calculate() } 
                </Typography>
            </Box>

            <Box 
                sx={{
                    marginLeft: {
                        xs: 3, // padding   xs ( 600px)
                        sm: 5, // padding   sm (600px  )
                        md: 15, // padding   md (960px  )
                        lg: 10, // padding   lg (1280px  )
                        xl: 15, // padding   xl (1920px  )
                    }
                }}
            >
                <Typography color="black"
                    sx={{
                        fontFamily: 'Kinetika, sans-serif',
                        ml: 1,
                        fontWeight: 400,
                        fontSize: '12px',
                        lineHeight: '1',
                        textAlign: 'left',
                        textTransform: 'uppercase',
                    }}
                >
                             &nbsp;
                    <a href="#table">  </a>
                </Typography>
            </Box>
        </Paper>
    );
}



export const DescriptionSegment =({ item, nameComponent }: DescriptionSegmentProps)=> {
    const theme = useTheme();
    const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));

    const handlerChangeInputs =(name: 'width'|'height'|'count', value: number)=> {
        console.log(name, value);
    }
    

    return(
        <div className="DescriptionTopCatalogItem">
            <Box 
                sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    //mx: 1
                }}
            >

                { nameComponent }

                {/*  */}
                <Typography
                    sx={{
                        fontFamily: 'Kinetika, sans-serif',
                        mt: 3,
                        fontWeight: 100,
                        fontSize: isSmallScreen ? '14px' : '16px',
                        lineHeight: '1.5',
                        maxWidth: '100%',
                        textOverflow: 'ellipsis',
                        whiteSpace: 'normal',
                       
                    }}
                >
                    { item.description }
                </Typography>

                {/*    */}
                <BlockPrice 
                    item={item}
                />

                {/*   */}
                <Box>
                    <Typography
                        sx={{
                            fontFamily: 'Kinetika, sans-serif',
                            mt: 3,
                            fontWeight: 100,
                            fontSize: '16px',
                            lineHeight: '1.5'
                        }}
                    >
                          :
                    </Typography>
                    <Box sx={{ 
                            mt: 4,
                            display: 'flex',
                            flexDirection: 'row',
                            gap: 2,
                        }}
                    >
                        <NumberInput 
                            label=', ' 
                            onChange={(v)=> handlerChangeInputs('width', v)} 
                        />
                        <NumberInput 
                            label=', ' 
                            onChange={(v)=> handlerChangeInputs('height', v)} 
                        />
                        <NumberInput 
                            label='' 
                            onChange={(v)=> handlerChangeInputs('count', v)} 
                            isAdornments={ !isSmallScreen } 
                        />
                    </Box>
                </Box>

                {/* */}
                <Button 
                    variant="contained" 
                    color="error" 
                    sx={{borderRadius:'16px', width:'100%',mt:2}}
                >
                       
                </Button>
            </Box>
        </div>
    );
}


/**
 *  display: '-webkit-box',         //    
                        WebkitBoxOrient: 'vertical',    //    
                        WebkitLineClamp: 3,             //    3 
 */